import React from 'react';
import { View, Text, StyleSheet, ImageBackground } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section1Page1 = () => {
const { language } = useLanguage();

return(
  <View style={styles.container}>
    <View style={styles.listContainer}>
      <Text style={styles.listItem}>
      {language === '' ? '   ' : '   '}
      </Text>
      <Text style={styles.listItem}>
      {language === '' ? '      ' : '     '}
      </Text>
      <Text style={styles.textItem}>
      {language === '' ? '(    , .1999, 12, . 296; .2004, 7, .451; .2007, 5, .352; .2008, 10, .797; .2012, 8, .811; .2014, 3, .140; 7, .1, .382; .2017, 7-8, .562; .2018, 2, .61; 9  10, .581; .2019, 7, 460)' : '(    , 1999.,  12, . 296, 2004.,  7, .451; 2007., 5, .352; 2008 ., 10, .797; 2012 ., 8, .811; 2014 ., 3, .140, 7, .1, .382; 2017., 7-8, .562; 2018., 2, .61; 9  10, .581; 2019 ., 7, .460)'}
      </Text>
    </View>

    <View style={styles.flagContainer}>
      <ImageBackground
        source={require('../images/parcham.png')}
        style={styles.flag}
        resizeMode="contain"
      >
        <View style={styles.overlay} />
      </ImageBackground>
    </View>
  </View>
);
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    paddingHorizontal: 20,
    paddingTop: 20,
    backgroundColor: '#fff',
    overflow: 'hidden',
  },
  header: {
    fontSize: 16,
    fontWeight: 'bold',
    textAlign: 'center',
    color: '#4A148C',
    marginTop: 20,
    marginBottom: 20,
  },
  listContainer: {
    alignItems: 'center',
    width: '100%',
    marginTop: '30',
},
listItem: {
    textAlign: 'center',
    fontSize: 18,
    fontWeight: 'bold',
    color: '#000',
    marginBottom: 15,
  },
  textItem: {
    fontSize: 19,
  },
  flagContainer: {
    marginTop: 20,
    width: '100%',
    alignItems: 'center',
  },
  flag: {
    width: '130%',
    height: 300,
    justifyContent: 'center',
    marginRight: '70'
  },
  overlay: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: 'rgba(255, 255, 255, 0.5)',
  },
});

export default Section1Page1;