import React from 'react';
import { Platform, View, Text, StyleSheet, ImageBackground } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section2Page1 = () => {
const { language } = useLanguage();

return(
  <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
    <View style={styles.listContainer}>
      <Text style={styles.listItem}>
      {language === '' ? '   ' : '   '}
      </Text>
      <Text style={styles.listItem}>
      {language === '' ? '        ' : '       '}
      </Text>
      <Text style={styles.textItem}>
      {language === '' ? ' (    , .1999,  12, . 298; .2007, 5, .353; .2012,8, .813; .2014, 7, .1, .383; 11, .640; .2017, 5, .1, .269; .2019, 7, . 461; .2024 1 - 2, . 1)' : ' (     1999 ,  12, .298; 2007 , 5, .353; 2012 , 8, .813; 2014 , 7, .1, .383; 11, .640, 2017 , 5, .1, .269; 2019 , 7, . 461   3  2024 , 2016)'}
      </Text>
    </View>

    <View style={styles.flagContainer}>
      <ImageBackground
        source={require('../images/parcham.png')}
        style={styles.flag}
        resizeMode="contain"
      >
        <View style={styles.overlay} />
      </ImageBackground>
    </View>
  </View>
);
};

const styles = StyleSheet.create({
  webcontainer:{
    paddingTop: 20,    
    overflowX: "hidden",
    paddingHorizontal: 20,
  },
  container: {
    flex: 1,
    alignItems: 'center',
    paddingHorizontal: 20,
    backgroundColor: '#fff',
  },
  header: {
    fontSize: 16,
    fontWeight: 'bold',
    textAlign: 'center',
    color: '#4A148C',
    marginTop: 20,
    marginBottom: 20,
  },
  listContainer: {
    alignItems: 'center',
    width: '100%',
    marginTop: '30',
},
listItem: {
    textAlign: 'center',
    fontSize: 20,
    fontWeight: 'bold',
    color: '#000',
    marginBottom: 15,
  },
  textItem: {
    fontSize: 21,
  },
  flagContainer: {
    width: '100%',
    alignItems: 'center',
  },
  flag: {
    width: '130%',
    height: 300,
    justifyContent: 'center',
    marginRight: '70'
  },
  overlay: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: 'rgba(255, 255, 255, 0.5)',
  },
});

export default Section2Page1;