import React from 'react';
import { View, Text, StyleSheet, Image } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section3Page1 = () => {
const { language } = useLanguage();

return(
  <View style={styles.container}>
       <Image
          source={require('../images/parcham.png')}
          style={styles.backgroundImage}
          resizeMode="contain"
        />
        <View style={styles.overlay} />
    <View style={styles.listContainer}>
      <Text style={styles.listItem}>
      {language === '' ? '  ' : '  '}
      </Text>
      <Text style={styles.listItem}>
      {language === '' ? '      ' : '     '}
      </Text>
      <Text style={styles.textItem}>
      {language === '' ? ' (    ,  2009, 7-8, .503;  2012,4,.277; 8,.833; 2015,11,.964;  2017, 5 1, .288;  2019, 4-5, .226) \n       ,          ,  ,      .' : '(    , 2009 , 7-8, . 503; 2012 , 4,.277; 8,.833; 2015 , 11,.964; 2017 , 5 1, .288; 2019., 4-5, .226) \n       ,            ,    .'}
      </Text>
    </View>
  </View>
);
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    paddingHorizontal: 20,
    backgroundColor: '#fff',
    overflowX: "hidden",
  },
  listContainer: {
    alignItems: 'center',
    width: '100%',
    marginTop: '20',
},
listItem: {
    textAlign: 'center',
    fontSize: 22,
    fontWeight: 'bold',
    color: '#000',
    marginBottom: 15,
  },
  textItem: {
    fontSize: 22,
  },
  backgroundImage: {
    position: 'absolute',
    top: '30%',
    width: '150%',
    left: -50,
    height: '40%',
    opacity: 0.3,
  },
  overlay: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: 'rgba(255, 255, 255, 0.5)',
  },
});

export default Section3Page1;