import React from 'react';
import { Platform, View, Text, StyleSheet, Image, ScrollView } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Aboutsection1 = () => {
const { language } = useLanguage();

return(
  <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
    <Image
      source={require('../images/parcham.png')}
      style={styles.backgroundImage}
      resizeMode="contain"
    />
    <View style={styles.overlay} />

    <ScrollView contentContainerStyle={styles.scrollContent}>
      <View style={styles.listContainer}>
        <Text style={styles.listItem}>
          {language === '' ? ' 1.       ' : ' 1.        '}
        </Text>
        <Text style={styles.textItem}>
          {language === '' ? '1)        (  )     ,    ,   ,              ,      ,               ,              . \n 2)    ,       ,    ,          . \n 3)    ,     ,             .' : '1)         (  )     ,       ,   ,          ,    ,                 ,            . \n 2)               ,        . \n 3)    ,     ,             .'}
        </Text>
        <Text style={styles.listItem}>
          {language === '' ? ' 2.          ' : ' 2.          '}
        </Text>
        <Text style={styles.textItem}>
          {language === '' ? '               ,          ,    ,     ,  .' : '                   ,      ,     ,  .'}
        </Text>
        <Text style={styles.listItem}>
          {language === '' ? ' 3.   ' : ' 3.   '}
        </Text>
        <Text style={styles.textItem}>
          {language === '' ? '           ,           .' : '          ,         .'}
        </Text>
        <Text style={styles.listItem}>
          {language === '' ? ' 4.    ' : ' 4.    '}
        </Text>
        <Text style={styles.textItem}>
          {language === '' ? '1)        .     ,         . \n 2)       ,      ,  ,  ,      .' : '1)     .             . \n 2)           ,       ,     .'}
        </Text>
        <Text style={styles.listItem}>
          {language === '' ? ' 5.   ' : ' 5.  '}
        </Text>
        <Text style={styles.textItem}>
          {language === '' ? '1)       -   . \n 2)         .' : '1)         . \n 2)        .'}
        </Text>
      </View>
    </ScrollView>
  </View>
  );
};

const styles = StyleSheet.create({
  webcontainer:{
    height: '100vh',
    overflow: 'scroll',
    paddingBottom: "20px",
    overflowX: 'hidden',
  },
  container: {
    flex: 1,
  },
  backgroundImage: {
    position: 'absolute',
    top: '30%',
    width: '150%',
    left: -50,
    height: '40%',
    opacity: 0.4,
  },
  overlay: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: 'rgba(255, 255, 255, 0.5)',
  },
  scrollContent: {
    paddingHorizontal: 20,
    paddingTop: 20,
  },
  listContainer: {
    alignItems: 'flex-start',
  },
  listItem: {
    fontSize: 23.6,
    fontWeight: 'bold',
    color: '#000',
    marginBottom: 15,
  },
  textItem: {
    fontSize: 22,
    marginBottom: 15,
  },
});

export default Aboutsection1;